@import '~assets/styles/h3Imports';

@mixin tablet-support {
  @media #{$tablet-up} {
    html[data-fontsize='AAA'] & {
      .button > :first-child {
        max-width: rem(120);
      }

      .info {
        margin-right: rem(16);
      }
    }

    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: rem(16);

    .info {
      padding-bottom: 0;
    }

    .button {
      align-items: center;
      white-space: nowrap;

      > :first-child {
        width: rem(195);
      }
    }

    .edit-button {
      margin-left: 0;
    }
  }
}

.root {
  background-color: white;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  display: flex;
  flex-direction: column;
  margin-bottom: rem(24);
  padding: rem(16);
  width: 100%;

  &[data-status='editable'] {
    @include success-card;
  }

  .info {
    align-items: center;
    display: inline-flex;
    padding-bottom: rem(16);

    span {
      margin-right: rem(16);
    }

    :first-child {
      svg {
        height: rem(48);
        width: rem(48);
      }
    }

    > :last-child {
      @include h5;

      color: var(--gray-5);
      font-size: rem(16);
      font-weight: 700;
      margin: 0;
    }
  }

  .button {
    > :first-child {
      font-size: rem(14);
      height: rem(40);
      padding-bottom: 0;
      padding-top: 0;
      width: 100%;
    }
  }

  .edit-button {
    @include paragraph;

    font-weight: 500;
    height: rem(40);
    margin-bottom: -10px;
    margin-left: -10px;
    margin-top: -10px;
    text-decoration: none;
  }

  @include tablet-support;
}
